import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { Card, Form, Input, Row, Col, Checkbox } from 'antd';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import {
  getAccountsReceivableById,
  createAccountReceivale,
  updateAccountReceivable
} from 'services/accountsReceivable';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
    name: '',
    type1: false,
    type2: false,
    type3: false,
    type4: false,
    price: 0,
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormAccountsReceivable' })
class AccountsReceivableEdit extends Component {
  state = {
    id: '',
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ id: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = async id => {
    const res = await getAccountsReceivableById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  }

  saveData = async values => {
    const { id } = this.state;
    try {
      if (!id) {
        await createAccountReceivale(values);
      } else {
        await updateAccountReceivable({ id, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const formItemCheckboxLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 20 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 4 },
      },
      colon: false,
    };
    return (
      <Card title="Thêm mới khoản thu">
        <Row>
          <Col span={24}>
            <Form onSubmit={this.handleSubmit}>
              <Form.Item label="Tên" {...formItemLayout}>
                {getFieldDecorator('name', {
                  initialValue: initialValue.name,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Form.Item label="Số tiền" {...formItemLayout}>
                {getFieldDecorator('price', {
                  initialValue: initialValue.price,
                  rules: [
                    {
                      required: true,
                      message: 'Vui lòng nhập thông tin!',
                    },
                  ],
                })(<Input />)}
              </Form.Item>
              <Row>
                <Col offset={5} span={12}>
                  <Col span={6}>
                    <Form.Item {...formItemCheckboxLayout} style={{ display: 'inline-block' }} label="Học phí">
                      {getFieldDecorator('type1', {
                        initialValue: initialValue.type1,
                      })(<Checkbox checked={initialValue.type1} />)}
                    </Form.Item>
                  </Col>
                  <Col span={6}>
                    <Form.Item {...formItemCheckboxLayout} style={{ display: 'inline-block' }} label="Kinh phí ĐT">
                      {getFieldDecorator('type2', {
                        initialValue: initialValue.type2,
                      })(<Checkbox />)}
                    </Form.Item>
                  </Col>
                  <Col span={6}>
                    <Form.Item {...formItemCheckboxLayout} style={{ display: 'inline-block' }} label="Học lại">
                      {getFieldDecorator('type3', {
                        initialValue: initialValue.type3,
                      })(<Checkbox />)}
                    </Form.Item>
                  </Col>
                  <Col span={6}>
                    <Form.Item {...formItemCheckboxLayout} style={{ display: 'inline-block' }} label="Thi lại">
                      {getFieldDecorator('type4', {
                        initialValue: initialValue.type4,
                      })(<Checkbox />)}
                    </Form.Item>
                  </Col>
                </Col>
              </Row>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Col>
        </Row>
      </Card>
    )
  }
}

export default withRouter(props => (
  <Page {...props}>
    <AccountsReceivableEdit {...props} />
  </Page>
));


